/**
 * @description Helpers for ext-bbm-personal-profile-phone-form-ng.
 * @name Helpers
 * @type {Object}
 */
export default ({ service, stateContainer, intents }) => {
  /**
   * @name Helpers#getPhoneFormData
   * @description Retrieves a phone's form data.
   * @type {function}
   *
   * @returns {module:model-bb-personal-profile-ng.Phone}
   */
  const getPhoneFormData = stateContainer.createSelector(state =>
    state.form.data.phone
  );

  /**
   * @name Helpers#updateUserPhone
   * @description Updates a user phone.
   * @type {function}
   *
   * @returns {Promise}
   */
  const updateUserPhone = () => (
    service.updateUserPhone()
      .then(() => {
        intents.showPersonalProfileDetails();
      })
  );

  /**
   * @name Helpers#isFormValid
   * @description Checks the form validation status.
   * @type {function}
   *
   * @param {object} form
   * @return {boolean}
   */
  const isFormValid = form => Boolean(form.$valid && form.$dirty);

  return {
    isFormValid,
    getPhoneFormData,
    updateUserPhone,
  };
};



