import { Intent } from './constants';

/**
 * @name Intents
 * @type {Object}
 */
export default ({ createRequest, handleRequest, service }) => ({
  /**
   * @name Intents#showPersonalProfileDetails
   * @description The intent to show the personal profile details.
   * @type {function}
   */
  showPersonalProfileDetails: createRequest(Intent.SHOW_PERSONAL_PROFILE_DETAILS),

  /**
   * @name Intents#showAddPhoneForm
   * @description Handler of the 'intent.bb.personalProfile.phone.add' intent.
   * @type {function}
   */
  showAddPhoneForm: handleRequest(Intent.SHOW_ADD_PHONE_FORM, () => {
    service.resetSelectedPhoneIfNeeded();
    service.setInitialPhoneForm();
  }),

  /**
   * @name Intents#showEditPhoneForm
   * @description Handler of the 'intent.bb.personalProfile.phone.edit' intent.
   * @type {function}
   */
  showEditPhoneForm: handleRequest(Intent.SHOW_EDIT_PHONE_FORM, phone => {
    if (phone) {
      service.setPhoneForm(phone);
    } else {
      throw new Error(`Phone is undefined.
        Please make sure to pass a phone object as the payload of the intent`);
    }
  }),
});



